.pnotify-pre-line {
  font-size: 17px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  margin: 20px 10px 20px 10px;
}

input[type='text'] {
  width: 300px;
  height: 50px;
  margin: 90px 0 0 90px;
  font-size: 30px;
}

.list {
  margin: 50px;
}

.country-item {
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 35px;
}

.contries {
  margin: 40px 0 0 90px;
}

.country-box {
  margin: 40px 30px;
  text-align: center;
  width: 700px;
}

.sec-box {
  text-align: initial;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 20px 10px;
}

.country-info {
  list-style: none;
  font-weight: bold;
  font-size: 30px;
}

.country-des {
  margin: 15px;
}

.language-item {
  margin: 10px 10px 10px 25px;
  font-weight: lighter;
  font-size: 23px;
}

img {
  border: 2px solid #000;
}
